import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
let {
	log
} = console;
const app = getApp().globalData;
const {
	appid,
	appsecret,
	imgRemote,
	navBar
} = app;
export default {
	data() {
		return {
			navBar: navBar,
			id: '',
			list: []
		};
	},
	methods: {
		leftClick() {
			uni.navigateBack({
				delta: 1
			});
		},
		cancelPay() {
			uni.switchTab({
				url: '/pages/tabar/shopcart'
			})
		},
		payOrder() {
			let {
				id
			} = this;
			let timeStamp = Math.round(new Date().getTime() / 1000);
			let obj = {
				appid: appid,
				timeStamp: timeStamp,
				oid: id,
				type: 'app'
			};
			let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			let params = Object.assign({
				sign: sign,
			}, obj)
			rs.postRequests("payTemOrder", params, (res) => {
				let {
					data
				} = res;
					log(res)
				if (data.code == 200) {
					if (data.data.payType == 1) {
						uni.showToast({
							title: '支付成功',
							duration: 2000,
							icon: "none"
						});
						// app.aData.pageClassify = false;
						wx.switchTab({
							url: '/pages/tabar/order',
						})
					}
					if (res.data.data.payType == 2) {
						let {
							wxParams
						} = res.data.data;
				
						uni.requestPayment({
							provider: 'wxpay',
							orderInfo: wxParams, //微信、支付宝订单数据
							success: function(res) {
								console.log('success:' + JSON.stringify(res));
								uni.showToast({
									title: '支付成功',
									duration: 2000,
									icon: "none"
								});
								// app.aData.pageClassify = false;
								wx.switchTab({
									url: '/pages/tabbar/order',
								})
							},
							fail: function(err) {
								console.log(err)
								uni.showToast({
									title: "充值失败",
									duration: 2000,
									icon: "none"
								});
							}
						});

					}
				}
			})
		},
		// 临时订单信息
		temOrder() {
			let timeStamp = Math.round(new Date().getTime() / 1000);
			let obj = {
				appid: appid,
				timeStamp: timeStamp,
				id: this.id
			};
			let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			let params = Object.assign({
				sign: sign,
			}, obj)
			rs.getRequests("temOrderMsg", params, (res) => {
				let {
					data
				} = res;
				if (data.code == 200) {
					this.list = data.data
				} else {
					uni.showToast({
						title: data.msg,
						duration: 2000,
						icon: "none"
					});
				}

			})
		}
	},
	onShow() {
		this.temOrder();
	},
	onLoad(option) {
		this.id = option.id;
	}
};
